.box-size{
    box-sizing: border-box;
}

.page-holder{
    margin-top: 2rem;
    text-align: center;
}

.page-holder>ul>.ant-pagination-item a{
    font-size: 10px;
    font-weight: bold;
    background-color: #ffffff;
    color: #545454;
}
.page-holder>ul>.ant-pagination-item-active a{
    font-size: 10px;
    font-weight: bold;
    color: white;
    background-color: #545454;
}

.page-holder>ul>.ant-pagination-item {
    font-size: 10px;
    font-weight: 500;
    background-color: #ffffff;
    color: #545454;
    border-color: #E8E8E8;
}
.page-holder>ul>.ant-pagination-item-active{
    font-size: 10px;
    font-weight: 500;
    color: white;
    background-color: 545454;
    border-color: #E8E8E8;
}

.page-holder>ul>.ant-pagination-next>button,
.page-holder>ul>.ant-pagination-prev>button{
    color: #545454;
    font-weight: bold;
    background-color: #ffffff;
    border: none;
    border-radius: none;
    -webkit-border-radius: none;
    -moz-border-radius: none;
    -ms-border-radius: none;
    -o-border-radius: none;
}

.page-holder>ul>.ant-pagination-options{
    display: none;
}

.icon-size{
    font-size: 50px;
    color: #1FB3AB;
    position: sticky;
    right: 0;
    left: 90vw;
    bottom: 5vh;
}
.tab-container> .ant-tabs>.ant-tabs-nav{ 
    border-bottom: none;
}
.tab-container>.ant-tabs-card > .ant-tabs-nav .ant-tabs-tab{
    border: none;
}
.tab-container>.ant-tabs-card > .ant-tabs-nav .ant-tabs-tab-active{
    background-color: #fafafa;
    color: #ffffff;
}
.tab-container>.ant-tabs-top > .ant-tabs-nav::before,
 .ant-tabs-bottom > .ant-tabs-nav::before, 
 .ant-tabs-top > div > .ant-tabs-nav::before, .ant-tabs-bottom > div > .ant-tabs-nav::before{
    border: none;
}

.tab-container>div>div>div>div>div>.ant-tabs-tab-btn{
    height: 100px;
    width: 150px;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}

.tab-pane{
    position: relative;
    border: none;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    
}
.tab-pane:active{
    color: rgb(1, 180, 187);
    background-color: rgb(1, 180, 187);

}

.tab-pane>.ant-card-body>button{
    position: absolute;
    top:5px;
    right:5px;
} 
   
