* {
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
  scroll-behavior: smooth;
}
.main {
  padding: 28px 7%;
  background: transparent linear-gradient(180deg, #ffffff 0%, #eaebef 100%) 0%
    0% no-repeat padding-box;
  box-shadow: 0px 2px 8px #00000014;
  height: auto;
}
.primary {
  width: 150px;
  height: 40px;
  border-radius: 25px;
  background-color: #1cb3bc;
  color: #fff;
  box-shadow: 0px 3px 8px #1cb3bc66;
  border: none;
}
.header .options {
  display: inline-block;
  margin: 10px 30px;
  vertical-align: top;
}
.header .options a {
  margin: 20px;
  text-decoration: none;
  color: #18478b;
  font-size: 16px;
  text-transform: uppercase;
}
.header .options2 {
  margin: 0 10px;
  float: right;
}
.header .options2 a {
  margin: 20px;
  text-decoration: none;
  color: #18478b;
  font-size: 16px;
}
.social,.info,.achievements {
  display: inline-block;
}
.vr {
  width: 1px;
  border-left: 2px solid #eaebef;
  margin: 15px 20px;
}
.social img {
  margin: 10px 5px;
}
.social p {
  letter-spacing: 1.4px;
  color: #4f5967;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  height: 100%;
  margin-left: 11px;
  font-size: 14px;
}
.info{
    vertical-align: middle;
    width: 200px;
    margin-top: -130px;
    margin-left: -70px;
}
.info h3{
    font-size: 24px;
    color: #1CB3BC;
    font-weight: bold;
}
.info h3 span{
    font-size: 16px;
    color: #4F5967;
    font-weight: 400;
}
.info .id{
    font-size: 14px;
    font-weight: bold;
    color: #707070;
}
.tagline{
    padding: 40px 0 ;
    display: grid;
    width: 400px;
    z-index: 9;
}
.tagline h1{
    font-size: 33px;
    color: #0C5CAA;
    text-shadow: 2px 2px 8px #00000029;
    z-index: 12;
}
.info h4{
    font-size: 16px;
    font-weight: bold;
    color: #4F5967;
}
.info p{
    color: #707070;
    font-size: 400;
    font-size: 14px;
    margin-bottom: 10px;
    width: 200px;
}
.person{
    display: inline-block;
    background-color: #fff;
    height: 450px;
    width: 360px;
    vertical-align: top;
    margin-top: 80px;
    padding: 10px;
    position: relative;
}
.person img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.person .play{
    width: 50px;
    height: 50px;
    position: absolute;
    left: 40%;
    top: 55%;
}
.person .badgetag{
    position: absolute;
    padding: 10px;
    background-color: #fff;
    border-radius: 7px;
    bottom: 30px;
    right: 5px;
}
.person .badgetag img{
    width: 45px;
    height: 50px;
}
.person .badgetag div{
    display: inline-block;
    vertical-align: top;
}
.person .badgetag h5{
    color: #0C5CAA;
    font-weight: bold;
    font-size: 18px;
}
.person .badgetag p{
    color: #0C5CAA;
    font-weight: 500;
    font-size: 16px;
}
.achievements{
    vertical-align: bottom;
    padding: 20px;
}
.achievements h2{
    color: #0C5CAA;
    font-size: 20px;
}
.achievements p{
    width: 350px;
    color: #707070;
    font-weight: 300;
    margin-top: 10px;
}
.achievements img{
    width: 70px;
}
.achievements .badge-info {
    margin-top: 20px;
    margin-bottom: 70px;
}
.achievements .badge-info div{
display: inline-block;
margin-right: 10px;
}
.achievements .badge-info .badge-title{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-size: 12px;
    color: #0C5CAA;
}
.by{
    font-size: 12px;
    color: #0C5CAA !important;
}


.section2,.section4{
    padding: 100px 10%;
    width: 100%;
}
.section2 div{
    display: inline-block;
}
.family{
    width: 100%;
}
.section2 h4,.section3 h4,.section4 h4,.section5 h4{
    font-size: 16px;
    color: #1CB3BC;
}
.section2 h1,.section3 h1,.section4 h1,.section5 h1{
    font-size: 28px;
    color: #0C5CAA;
    width: 490px;
}
.section2 p{
    width: 60%;
    font-size: 12px;
    font-weight: 300;
    color: #707070;
}
.planHold div{
    width: 100px;
    text-align: center;
    vertical-align: top;
}
.planHold img{
    width: 40px;
    vertical-align: top;
}
.planHold p{
    margin-top: 10px;
    width: 100%;
}

