    display: inline-block;
}
.section3 .imgdata{
width: 50%;
display: inline-block;
vertical-align: top;
padding:100px 0 ;
}
.imgdata .cont{
    display: inline-block;
}
.productoptions{
    padding: 15px;
    border-radius: 8px;
    border: #4F596766 1px solid;
    display: flex;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
    margin-bottom: 20px;
    position: relative;
}
.selectedproduct{
    background-color: #23B2BA;
    box-shadow: 0px 6px 12px #1CB3BC4D;
    color: #fff !important;
    border: none;
