}
.matter{
    color: #707070;
    font-size: 12px;
}
.matter span svg{
    vertical-align: bottom;
    margin-right: 10px;
    fill: #0C5CAA;
}
.productoptions svg{
    position: absolute;
    right: 15px;
    top: 20px;
}


.section4{
background-color: #EDEEEF;
}
.cardsHolder{
    width: 100%;
    margin-top: 30px;
}
.cardsHolder .card{
    width: 300px;
    height: 330px;
    border-radius: 10px;
    border: 1px solid #4F596733;
    box-shadow: 16px 16px 36px #0000000D;
    background-color: #fff;
    display: inline-block;
    margin-right: 10px;
}
.cardsHolder .card img{
    width: 100%;
    height: 45%;
    object-fit: cover;
}
.cardsHolder .card .cardCont{
    padding: 10px;
}
.cardsHolder .card .cardCont h4{
    color: #0C5CAA;
    font-weight: 500;
    margin-bottom: 10px;
}
.cardsHolder .card .cardCont p{
    color: #4F5967;
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 20px;

}
.cardsHolder .card .cardCont a{
    text-decoration: none;
    color: #1CB3BC;
    font-size: 12px;
}
.section5{
    padding: 100px 10% 0;
    width: 80%;
}
.section5 div{
    display: inline-block;
    vertical-align: top;
}

.section5 .imagehld{
    min-height: 600px;
